.navbar .nav-link:focus {
  outline: none;
}

.navbar .dropdown-toggle {
  position: relative;
}

.navbar .dropdown-toggle::after {
  position: absolute;
  display: inline-block;
  width: 18px;
  height: 18px;
  top: 10px;
  right: 2px;
  margin-left: 0;
  transition: 0.2s;
  border: none;
}

.navbar .dropdown-toggle[aria-expanded="true"]::after {
  transform: rotate(-180deg);
}

