  color: #212529;
  background: none;
}

.svg-icon.navbar-icon {
  width: 1.5rem;
  height: 1.5rem;
}

.navbar-icon-badge {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 20px;
  height: 20px;
  text-align: center;
  color: #fff;
  border-radius: 50%;
  background: #bcac76;
  font-size: 0.6rem;
  line-height: 20px;
}

/*
*
* =====================
* Dropdowns
* =====================
*
*/
.dropdown-menu-animated.dropdown-menu-end {
  right: 0 !important;
  left: auto !important;
}

.dropdown-menu-animated {
  position: absolute;
  margin: 0;
  animation-name: AnimateDropdown;
  animation-duration: .3s;
  animation-fill-mode: both;
  z-index: 1000;
}

.dropdown-menu-animated.megamenu {
  animation-name: AnimateDropdownMegamenu;
}

.dropdown-menu-animated.show {
  top: 100% !important;
}

.dropdown-menu-animated i {
  display: inline-block;
}

.dropdown-menu-animated.dropdown-menu[data-popper-placement^=left],
.dropdown-menu-animated.dropdown-menu[data-popper-placement^=right],
.dropdown-menu-animated.dropdown-menu[data-popper-placement^=top] {
  top: auto !important;
  animation: none !important;
}

@keyframes AnimateDropdown {
  100% {
    transform: translateY(0);
    opacity: 1;
  }
  0% {
    transform: translateY(10px);
    opacity: 0;
  }
}

@keyframes AnimateDropdownMegamenu {
  100% {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }
  0% {
    transform: translateX(-50%) translateY(10px);
    opacity: 0;
  }
}

