@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .card {
    display: block;
  }
  .card-img-top,
  .card-img-bottom,
  .card-img {
    min-width: 100%;
    height: auto;
  }
  .product-stars,
  .product-hover-overlay {
    transition: none;
  }
  .swiper-thumb-item {
    width: 100px;
  }
  .sidebar-cart-footer {
    max-width: 500px;
  }
  .navbar .input-group-underlined {
    min-width: 200px;
  }
}

/*
* ===================================================
*     Docs
* ===================================================
*/
.docs-item {
  margin-bottom: .5rem;
  padding: 2.5rem 0;
  border-bottom: 1px solid #dee2e6;
}

.docs-item:first-of-type {
  padding-top: 0;
}

.docs-item:last-of-type {
  border-bottom: none;
}

.docs-item .docs-desc {
  color: #868e96;
}

.docs-item .docs-desc p.lead,
.docs-item .docs-desc strong,
.docs-item .docs-desc h1,
.docs-item .docs-desc .h1,
.docs-item .docs-desc h2,
.docs-item .docs-desc .h2,
.docs-item .docs-desc h3,
.docs-item .docs-desc .h3,
.docs-item .docs-desc h4,
.docs-item .docs-desc .h4,
.docs-item .docs-desc h5,
.docs-item .docs-desc .h5,
.docs-item .docs-desc h6,
.docs-item .docs-desc .h6 {
  color: #212529;
}

/*

=====================
STYLE SWITCHER FOR DEMO
=====================

*/
#style-switch-button {
  position: fixed;
  top: 160px;
  right: 0px;
  border-radius: 0;
  z-index: 2;
}

.style-switch-select {
  height: auto !important;
}

#style-switch {
  position: fixed;
  top: 192px;
  right: 0;
  width: 300px;
  padding: 20px;
  border: solid 1px #ced4da;
  background: #fff;
  z-index: 2000;
}

#style-switch h4, #style-switch .h4 {
  color: #495057;
}

.feature-icon {
  width: 64px;
  height: 64px;
  stroke-width: 2;
}

.page-label {
  display: inline-block;
  position: absolute;
  top: -20px;
  right: -20px;
  width: 80px;
  height: 80px;
  text-align: center;
  letter-spacing: .2em;
  text-transform: uppercase;
  color: #fff;
  border-radius: 100%;
  background: #d3122a;
  font-size: 12px;
  font-weight: 600;
  line-height: 80px;
  z-index: 99;
}
