/*
* ===================================================
*     SVG icons
* ===================================================
*/

.svg-icon {
    width: 18px;
    height: 18px;
    vertical-align: text-bottom;
    stroke: currentColor;
    stroke-width: 3;

    --layer1: currentColor;
    --layer2: currentColor;

    &.svg-icon-light {
        stroke-width: 2;
    }

    &.svg-icon-heavy {
        stroke-width: 4;
    }

}


.svg-icon-sm {
    width: 16px;
    height: 16px;
}

.svg-icon-md {
    width: 24px;
    height: 24px;
}

.svg-icon-lg {
    width: 48px;
    height: 48px;
}

.svg-icon-xl {
    width: 64px;
    height: 64px;
}