/*
* ===================================================
*     Megamenu
* ===================================================
*/
.dropdown-menu.megamenu {
    left: 50%;
    overflow: hidden;
    width: 100%;
    max-width: 1300px;
    margin-top: -2px;
    border: none !important;
    z-index: 9999;

    .megamenu-list {
        margin-bottom: 1.5rem;
        padding: 0;
        font-size: 0.8rem;
    }

    .megamenu-list-item {
        margin-bottom: 5px;
    }

    .megamenu-list-link {
        padding: 5px 0;
        text-decoration: none;
        color: $gray-600;

        &:hover {
            color: $gray-900;
            background: none;
        }
    }

}

.svg-icon.navbar-icon {
    width: 1.5rem;
    height: 1.5rem;
}

// shopping cart badge with no. of items
.navbar-icon-badge {
    position: absolute;
    top: -10px;
    right: -10px;
    width: 20px;
    height: 20px;
    text-align: center;
    color: $white;
    border-radius: 50%;
    background: $primary;
    font-size: 0.6rem;
    line-height: 20px;
}